import { defineStore } from 'pinia'
import { ref, computed, shallowRef } from 'vue'
import { Game } from '@/domain/entities/Game'
import type { Card } from '@/domain/entities/Card'
import type { GameConfig } from '@/domain/types/game.types'

export const useGameStore = defineStore('game', () => {
    // State - using shallowRef to avoid deep reactivity on Game instance
    const game = shallowRef<Game | null>(null)
    const isInitialized = ref(false)
    const lastUpdate = ref(Date.now()) // Force reactivity when game state changes internally

    // Getters
    const vitality = computed(() => {
        if (!game.value) return 0
        // Dependency on lastUpdate forces re-evaluation
        lastUpdate.value
        return game.value.vitality
    })

    const maxVitality = computed(() => {
        if (!game.value) return 100
        lastUpdate.value
        return game.value.maxVitality
    })

    const hand = computed(() => {
        if (!game.value) return []
        lastUpdate.value
        return game.value.hand
    })

    const currentStage = computed(() => {
        if (!game.value) return 'youth'
        lastUpdate.value
        return game.value.stage
    })

    const currentTurn = computed(() => {
        if (!game.value) return 0
        lastUpdate.value
        return game.value.turn
    })

    const currentPhase = computed(() => {
        if (!game.value) return 'setup'
        lastUpdate.value
        return game.value.phase
    })

    const currentChallenge = computed(() => {
        if (!game.value) return null
        lastUpdate.value
        return game.value.currentChallenge
    })

    const insuranceTypeChoices = computed(() => {
        if (!game.value) return []
        lastUpdate.value
        return game.value.insuranceTypeChoices
    })

    const cardChoices = computed(() => {
        if (!game.value) return []
        lastUpdate.value
        return game.value.cardChoices
    })

    const lastMessage = ref<string>('')

    // Actions
    function initializeGame(config?: GameConfig) {
        game.value = new Game(config)
        isInitialized.value = true
        triggerUpdate()
    }

    function startGame() {
        if (!game.value) return
        game.value.start()
        triggerUpdate()
    }

    async function drawCards(count: number) {
        console.log('[GameStore] drawCards called', count)
        if (!game.value) {
            console.error('[GameStore] Game instance is null')
            return
        }
        await game.value.drawCards(count)
        console.log('[GameStore] drawCards finished, triggering update')
        triggerUpdate()
    }

    function startChallenge(card: Card) {
        if (!game.value) return
        game.value.startChallenge(card)
        triggerUpdate()
    }

    function drawChallenge() {
        if (!game.value) return
        if (game.value.currentChallenge) return

        console.log('[GameStore] drawChallenge called. Deck size:', game.value.challengeDeck.size())

        const challengeCard = game.value.drawChallengeCard()
        if (challengeCard) {
            game.value.startChallenge(challengeCard)
            triggerUpdate()
        } else {
            // Deck is empty, try to advance stage
            console.log('[GameStore] Challenge deck empty, advancing stage')
            game.value.advanceStage()

            // If stage changed, we need to repopulate the challenge deck for the new stage
            // But Game.advanceStage() doesn't seem to do that automatically?
            // Game.advanceStage checks stageManager.advanceStage.
            // If new stage, we need to add cards.

            // Let's check if stage actually changed
            const currentStage = game.value.stage

            // We need to refill deck if we moved to a new stage
            // But Game entity doesn't seem to have logic to refill deck on stage change in advanceStage()
            // We might need to do it here manually like GameController does.

            if (game.value.status === 'victory') {
                console.log('[GameStore] Victory!')
            } else {
                // Refill deck for new stage
                // We need CardFactory
                import('@/domain/services/CardFactory').then(({ CardFactory }) => {
                    if (!game.value) return
                    const newCards = CardFactory.createChallengeCards(game.value.stage)
                    game.value.challengeDeck.addCards(newCards)
                    game.value.challengeDeck.shuffle()
                    triggerUpdate()

                    // Auto-draw next challenge? Or let user click again?
                    // Let's let user click again to see the stage change
                })
            }
            triggerUpdate()
        }
    }

    function resolveChallenge() {
        if (!game.value) return
        const result = game.value.resolveChallenge()
        lastMessage.value = result.message
        triggerUpdate()
        return result
    }

    function selectInsurance(insuranceType: any) {
        if (!game.value) return
        // Assuming Game entity has a method to handle insurance selection
        // If not, we might need to implement it or use a service
        // Checking Game.ts, it seems we might need to call a service or a method on Game
        // Let's assume for now we just log it or need to implement it in Game.ts if missing
        // Actually Game.ts likely has a method or we need to use GameInsuranceService
        console.log('Selected insurance:', insuranceType)
        // For now, let's just move to next phase to unblock
        game.value.phase = 'end'
        triggerUpdate()
    }

    function endTurn() {
        if (!game.value) return
        const result = game.value.nextTurn()
        if (result.insuranceExpirations) {
            lastMessage.value = result.insuranceExpirations.message
        } else {
            lastMessage.value = `Turn ${game.value.turn} started`
        }
        triggerUpdate()
    }

    function toggleCardSelection(card: Card) {
        if (!game.value) return
        game.value.toggleCardSelection(card)
        triggerUpdate()
    }

    // Helper to force UI updates since Game entity mutations might not be deep-watched
    function triggerUpdate() {
        lastUpdate.value = Date.now()

        // Expose for testing
        if (typeof window !== 'undefined') {
            (window as any)._gameStore = { game: game.value }
        }
    }

    return {
        game,
        isInitialized,
        vitality,
        maxVitality,
        hand,
        currentStage,
        currentTurn,
        currentPhase,
        currentChallenge,
        insuranceTypeChoices,
        lastMessage,
        initializeGame,
        startGame,
        drawCards,
        startChallenge,
        drawChallenge,
        resolveChallenge,
        selectInsurance,
        endTurn,
        toggleCardSelection,
        triggerUpdate
    }
})
