/**
 * Vue I18n 
 * 
 */

import { createI18n } from 'vue-i18n'
import type { I18nOptions } from 'vue-i18n'

// 
import ja from './locales/ja'
import en from './locales/en'
import zh from './locales/zh'
import ko from './locales/ko'

// 
export const SUPPORTED_LOCALES = ['ja', 'en', 'zh', 'ko'] as const
export type SupportedLocale = typeof SUPPORTED_LOCALES[number]

// 
export const LOCALE_NAMES: Record<SupportedLocale, string> = {
  ja: '',
  en: 'English',
  zh: '',
  ko: ''
}

// RTL
export const RTL_LOCALES: SupportedLocale[] = []

// 
export function detectBrowserLocale(): SupportedLocale {
  const browserLang = navigator.language.split('-')[0] as SupportedLocale
  return SUPPORTED_LOCALES.includes(browserLang) ? browserLang : 'ja'
}

// 
export function getSavedLocale(): SupportedLocale | null {
  try {
    const saved = localStorage.getItem('insurance-game-locale') as SupportedLocale
    return SUPPORTED_LOCALES.includes(saved) ? saved : null
  } catch {
    return null
  }
}

// 
export function saveLocale(locale: SupportedLocale): void {
  try {
    localStorage.setItem('insurance-game-locale', locale)
  } catch (error) {
    console.warn('Failed to save locale to localStorage:', error)
  }
}

// 
export function getInitialLocale(): SupportedLocale {
  return getSavedLocale() || detectBrowserLocale()
}

// i18n
const i18nOptions: I18nOptions = {
  legacy: false, // Composition API mode
  locale: getInitialLocale(),
  fallbackLocale: 'ja',
  messages: {
    ja,
    en,
    zh,
    ko
  },
  // 
  numberFormats: {
    ja: {
      currency: {
        style: 'currency',
        currency: 'JPY',
        currencyDisplay: 'symbol'
      },
      decimal: {
        style: 'decimal',
        minimumFractionDigits: 0,
        maximumFractionDigits: 2
      }
    },
    en: {
      currency: {
        style: 'currency',
        currency: 'USD',
        currencyDisplay: 'symbol'
      },
      decimal: {
        style: 'decimal',
        minimumFractionDigits: 0,
        maximumFractionDigits: 2
      }
    },
    zh: {
      currency: {
        style: 'currency',
        currency: 'CNY',
        currencyDisplay: 'symbol'
      },
      decimal: {
        style: 'decimal',
        minimumFractionDigits: 0,
        maximumFractionDigits: 2
      }
    },
    ko: {
      currency: {
        style: 'currency',
        currency: 'KRW',
        currencyDisplay: 'symbol'
      },
      decimal: {
        style: 'decimal',
        minimumFractionDigits: 0,
        maximumFractionDigits: 0
      }
    }
  },
  // 
  datetimeFormats: {
    ja: {
      short: {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      },
      long: {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'long'
      }
    },
    en: {
      short: {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      },
      long: {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'long'
      }
    },
    zh: {
      short: {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      },
      long: {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'long'
      }
    },
    ko: {
      short: {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      },
      long: {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'long'
      }
    }
  }
}

// i18n
export const i18n = createI18n(i18nOptions)

// 
export function setLocale(locale: SupportedLocale): void {
  if (!SUPPORTED_LOCALES.includes(locale)) {
    console.warn(`Unsupported locale: ${locale}`)
    return
  }
  
  i18n.global.locale.value = locale
  saveLocale(locale)
  
  // HTML lang
  document.documentElement.lang = locale
  
  // RTL
  document.documentElement.dir = RTL_LOCALES.includes(locale) ? 'rtl' : 'ltr'
}

// 
export function getCurrentLocale(): SupportedLocale {
  return i18n.global.locale.value as SupportedLocale
}

// 
export type MessageSchema = typeof ja

export default i18n